class ToastService {
  constructor() {
    if (!toasts) toasts = this;
    this.toasts = [];
    return toasts;
  }

  addToast = (toast) => {
    if (this.toasts.length < 3) {
      this.toasts.push(toast);
    }
  };

  generateToast = (...options) => {
    console.log("bbbbb");
    return <Notification {...options} />;
  };

  getAllToasts = () => {
    return this.toasts;
  };
}

export const toast = new ToastService();
